<template>
  <div class="form-create-order">
    <vue-form>
      <div class="form__switcher">
        <label>Кто нужен?</label>
        <vue-switcher v-model="orderModel.orderSwitcherParams" />
      </div>
      <div
        class="first-option"
        v-if="orderModel.orderSwitcherParams[0].isSwitch === true"
      >
        <div class="form__location" v-if="!orderModel.orderCheckBoxParams">
          <label>Адрес</label>
          <vue-text-input
            v-model="orderModel.locationEnd"
            :placeholder="'Адрес'"
          />
        </div>
        <div class="form__date">
          <label>На какое число заказ?</label>
          <vue-number-input
            v-model="orderModel.inputDate.date"
            v-model:is-valid="orderModel.inputDate.isValid"
            :placeholder="'01.01.2025'"
            :type="'date'"
          />
        </div>
        <div class="form__time">
          <label>В какое время нужно прибыть?</label>
          <vue-number-input
            v-model="orderModel.inputTime.time"
            v-model:is-valid="orderModel.inputTime.isValid"
            :type="'time'"
            :placeholder="'12:00'"
          />
        </div>
        <div class="form__people">
          <label>Нужное количество грузчиков</label>
          <vue-number-input
            v-model="orderModel.inputPeople.peopleCount"
            :placeholder="'Кол-во'"
            v-model:is-valid="orderModel.inputPeople.isValid"
          />
        </div>
        <div class="form__description">
          <label>Описание проекта</label>
          <vue-text-area
            v-model="orderModel.orderDescription.description"
            v-model:is-valid="orderModel.orderDescription.isValid"
            :placeholder="'Что нужно сделать?'"
          />
        </div>
        <div class="form__working-time">
          <label>Ориентировочное время занятости (в минутах)</label>
          <vue-number-input
            v-model="orderModel.inputWorkingTime.time"
            v-model:is-valid="orderModel.inputWorkingTime.isValid"
            :placeholder="'60'"
          />
        </div>
        <div class="form__price">
          <label>Цена за заказ (руб)</label>
          <vue-number-input
            v-model="orderModel.inputPrice.price"
            v-model:is-valid="orderModel.inputPrice.isValid"
            :placeholder="'500'"
          />
        </div>
        <div class="form__save-template" v-loading="isLoadingSaveTemplate">
          <vue-black-button
            :title="'Сохранить как шаблон'"
            @click="onSaveTemplate"
          />
        </div>
        <div class="form__submit" v-loading="isLoadingCreateOrder">
          <vue-yellow-button :title="'Создать'" @click="onCreateOrder" />
        </div>
        <div class="form__substitute">
          <vue-green-button
            :title="'Подставить шаблон'"
            @click="isVisibleModal = !isVisibleModal"
          />
        </div>
      </div>

      <div
        class="second-option"
        v-if="orderModel.orderSwitcherParams[1].isSwitch === true"
      >
        <div class="form__location-transportation__start">
          <label>Пункт "А"</label>
          <vue-text-input
            v-model="orderModel.locationStart"
            :placeholder="'Адрес'"
          />
        </div>
        <div class="form__location-transportation__end">
          <label>Пункт "Б"</label>
          <vue-text-input
            v-model="orderModel.locationEnd"
            :placeholder="'Адрес'"
          />
        </div>
        <div class="form__date">
          <label>На какое число заказ?</label>
          <vue-number-input
            v-model="orderModel.inputDate.date"
            v-model:is-valid="orderModel.inputDate.isValid"
            :placeholder="'01.01.2025'"
            :type="'date'"
          />
        </div>
        <div class="form__time">
          <label>В какое время нужно прибыть?</label>
          <vue-number-input
            v-model="orderModel.inputTime.time"
            v-model:is-valid="orderModel.inputTime.isValid"
            :type="'time'"
            :placeholder="'12:00'"
          />
        </div>
        <div class="form__people">
          <label>Нужное количество грузчиков (если нет, то впишите 0)</label>
          <vue-number-input
            v-model="orderModel.inputPeople.peopleCount"
            v-model:is-valid="orderModel.inputPeople.isValid"
            :placeholder="'Кол-во'"
          />
        </div>
        <div class="form__description">
          <label>Что перевозить?</label>
          <vue-text-area
            v-model="orderModel.orderDescription.description"
            v-model:is-valid="orderModel.orderDescription.isValid"
            :placeholder="'Что нужно сделать?'"
          />
        </div>
        <div class="form__price">
          <label>Цена за заказ (руб)</label>
          <vue-number-input
            v-model="orderModel.inputPrice.price"
            v-model:is-valid="orderModel.inputPrice.isValid"
            :placeholder="'500'"
          />
        </div>
        <div class="form__save-template" v-loading="isLoadingSaveTemplate">
          <vue-black-button
            :title="'Сохранить как шаблон'"
            @click="onSaveTemplate"
          />
        </div>
        <div class="form__submit" v-loading="isLoadingCreateOrder">
          <vue-yellow-button :title="'Создать'" @click="onCreateOrder" />
        </div>
        <div class="form__substitute">
          <vue-green-button
            :title="'Подставить шаблон'"
            @click="isVisibleModal = !isVisibleModal"
          />
        </div>
      </div>
      <vue-modal
        :is-visible="isVisibleModal"
        @close-modal="isVisibleModal = $event"
      >
        <div class="selection-template">
          <div class="selection-template__title h3">
            <h3>Использовать шаблон:</h3>
          </div>
          <div class="selection-template__items">
            <div
              class="selection-template__item"
              v-for="template of templateStore.templates"
              :key="template.id"
              @click="autoFillTemplate(template)"
            >
              <p>{{ template.name }}</p>
            </div>
          </div>
        </div>
      </vue-modal>
      <vue-modal
        :is-visible="isSaveAsTemplate"
        @close-modal="isSaveAsTemplate = $event"
      >
        <div class="created-template">
          <vue-text-input
            v-model="templateName"
            :placeholder="'Введите название шаблона'"
          />
          <vue-black-button :title="'Сохранить'" @click="onSaveTemplate" />
        </div>
      </vue-modal>
    </vue-form>
  </div>
</template>

<script setup lang="ts">
import VueForm from "@/components/Forms/VueForm.vue";
import VueTextInput from "@/components/Inputs/VueTextInput.vue";
import VueTextArea from "@/components/Inputs/VueTextArea.vue";
import VueSwitcher from "@/components/Switchers/VueSwitcher.vue";
import VueNumberInput from "@/components/Inputs/VueNumberInput.vue";
import VueYellowButton from "@/components/Buttons/VueYellowButton.vue";
import VueBlackButton from "../Buttons/VueBlackButton.vue";
import VueGreenButton from "../Buttons/VueGreenButton.vue";
import VueModal from "../Modals/VueModal.vue";

import { reactive, ref } from "vue";
import { userTemplatesStore } from "@/store/template";
import { Template } from "@/interfaces/Template";
import notification from "@/notifications";
import { TemplateBase } from "@/interfaces/TemplateBase";

const templateStore = userTemplatesStore();
let isVisibleModal = ref(false);
let isSaveAsTemplate = ref(false);
const templateName = ref("");
const isLoadingCreateOrder = ref(false);
const isLoadingSaveTemplate = ref(false);

const orderModel = reactive({
  orderSwitcherParams: [
    {
      id: 1,
      value: "Грузчики",
      isSwitch: false,
    },
    {
      id: 2,
      value: "Грузоперевозки",
      isSwitch: false,
    },
  ],
  orderDescription: {
    description: "",
    isValid: false,
  },
  locationStart: "",
  locationEnd: "",
  inputPeople: {
    peopleCount: <number | null>null,
    isValid: false,
  },
  inputPrice: {
    price: <number | null>null,
    isValid: false,
  },
  inputDate: {
    date: "",
    isValid: false,
  },
  inputWorkingTime: {
    time: <number | null>null,
    isValid: false,
  },
  inputTime: {
    time: "",
    isValid: false,
  },
  orderCheckBoxParams: null,
});

const onSaveTemplate = () => {
  isSaveAsTemplate.value = !isSaveAsTemplate.value;

  if (templateName.value) {
    isLoadingSaveTemplate.value = true;

    const model = createTemplate();
    templateStore.createdTemplate(model);

    //TODO(tolstov): симуляция запроса
    setTimeout(() => {
      isLoadingSaveTemplate.value = false;
      notification.success("Сохранен как шаблон!");
    }, 1000);
  } else {
    isSaveAsTemplate.value = true;
  }
};

const createTemplate = () => {
  const model: TemplateBase = {
    locationStart: orderModel.locationStart,
    locationEnd: orderModel.locationEnd,
    time: orderModel.inputTime.time,
    name: templateName.value,
    sumPeople: orderModel.inputPeople.peopleCount,
    description: orderModel.orderDescription.description,
    workers: {
      value: orderModel.orderSwitcherParams[0].isSwitch
        ? "Грузчики"
        : "Грузоперевозки",
    },
    workingHours: Number(orderModel.inputWorkingTime.time),
    priceInfo: {
      price: orderModel.inputPrice.price,
    },
  };
  return model;
};

const autoFillTemplate = (t: Template) => {
  //заполняем поля из шаблона
  if (t.workers.value === "Грузчики") {
    orderModel.orderSwitcherParams[0].isSwitch = true;
    orderModel.orderSwitcherParams[1].isSwitch = false;
  } else if (t.workers.value === "Грузоперевозки") {
    orderModel.orderSwitcherParams[1].isSwitch = true;
    orderModel.orderSwitcherParams[0].isSwitch = false;
  }

  orderModel.orderDescription.description = t.description;
  orderModel.locationStart = t.locationStart ? t.locationStart : "";
  orderModel.locationEnd = t.locationEnd;
  orderModel.inputPeople.peopleCount = t.sumPeople;
  orderModel.inputPrice.price = t.priceInfo.price;
  orderModel.inputWorkingTime.time = t.workingHours;
  orderModel.inputTime.time = t.time;

  isVisibleModal.value = false;
};

const onCreateOrder = () => {
  isLoadingCreateOrder.value = true;

  //TODO(tolstov): симуляция запроса
  setTimeout(() => {
    isLoadingCreateOrder.value = false;
    notification.success("Заказ успешно создан!");
  }, 1000);
};
</script>

